body{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    height: 100vh;
    display: flex;
    justify-content: center;
    /* align-items: center; */
    text-align: center;
    background-color: #b8d7d7;
}
.main_div{
    background-color: #cbc63a;
    width: 31vw;
    min-height: 60vh;
    padding: 3%;
    margin-top: 17%;
    border-radius: 6%;
    border: 2px solid #a3cf1d;
    box-shadow: 0rem 0.5rem 1rem #767547
}
.todo_div{
    padding: 8px;
    font-size: 26px;
    color: #b30042;
    text-shadow: 1px 2px 3px #8c518d;
    font-family: cursive;
    font-weight: 700;
}
.main_input_div{
    padding: 20px 0px;
    display: flex;
    justify-content: space-evenly;
}
.input{
    padding: 6px 40px;
    font-size: 17px;
    text-align: center;
    box-shadow: 0px 3px 6px #7a7878;
    color: rgb(100, 45, 45)(145, 75, 75);
    border: 2px solid #b9df97;
    border-radius: 2%;
    background-color: #f9f9f9;
}
.add_button{
    padding: 5px 17px;
    border: none;
    font-size: 17px;
    font-weight: 700;
    box-shadow: 0px 2px 5px #666363;
    /* font-family: cursive; */
    cursor: pointer;
    background-color: #35a5b1;
    color: #ffffff;
    border-radius: 4px;
}
.todo_item{
    padding: 6px;
    width: 29vw;
    max-height: 36vh;
    /* text-shadow: 0px 2px 3px rgb(213 205 215); */
    overflow: auto;
    border-radius: 18px;
    /* text-align: center; */
    /* align-items: center; */
    color: rgb(47 149 139);
    /* background-color: #f9b9b9; */
    /* scroll-behavior: auto; */
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}